
import { 
    Text, 
    Box, 
    Flex, 
    InputGroup, 
    NumberInput,
    NumberInputField,
    NumberInputStepper,
    NumberIncrementStepper,
    NumberDecrementStepper,
    Switch,
    Checkbox,
    IconButton,
    useColorMode,
} from '@chakra-ui/react'
import {
    MoonIcon,
    SunIcon
} from "@chakra-ui/icons";
import LevelExperience from './LevelExperience';
import { useState, useEffect } from "react"
import XPOptions from "./XPOptions";

const calculateLevel = (xp) => {
    let closestLevel;

    for (let level in LevelExperience) {
        let experience = LevelExperience[level];

        if (experience <= xp) {
            closestLevel = level;
        } else {
            break;
        }
    }

    return closestLevel;
};

const CalculatorUI = () => {
    const [ currentXP, setCurrentXP ] = useState(LevelExperience[1]);
    const [ targetXP, setTargetXP ] = useState(LevelExperience[2]);
    const [ xpDiff, setXPDiff ] = useState(targetXP-currentXP);
    const [ currentLevel, setCurrentLevel ] = useState(calculateLevel(currentXP));
    const [ targetLevel, setTargetLevel ] = useState(calculateLevel(targetXP));
    const [ experienceSwitch, setExperienceSwitch ] = useState(false);
    const [ XPMultiplier, setXPMultiplier ] = useState(1);
    const { colorMode, toggleColorMode } = useColorMode();
    const [ selectedCheckbox, setSelectedCheckbox ] = useState(null);

    const handleCheckboxChange = (id, val) => {
        if (selectedCheckbox === id) {
            setSelectedCheckbox(null);
            changeMultiplier(1);
        } else {
            setSelectedCheckbox(id);
            changeMultiplier(val);
        }
    };

    const changeMultiplier = (val) => {
        setXPMultiplier(val);
        return val;
    };
    
    const toggleXPSwitch = (e) => {
        setExperienceSwitch(e.target.checked);
    };

    useEffect(() => {
        setXPDiff(targetXP-currentXP);
        setCurrentLevel(calculateLevel(currentXP));
        setTargetLevel(calculateLevel(targetXP));
    }, [currentXP, targetXP, XPMultiplier]);

    return (
        <Box m={5} p={5} borderWidth='1px' borderRadius='lg' overflow="scroll">
            <Flex align="center" justifyContent="space-between">
                <Text paddingBottom="10px" fontSize="2xl">Prayer XP Calculator</Text>
                <Flex justifyContent="center" align="center" gap={2}>
                    <Text>
                        XP/Level Toggle
                    </Text>
                    <Switch pt={1} size="md" isChecked={experienceSwitch} onChange={(e) => toggleXPSwitch(e)} />
                </Flex>
            </Flex>
            <Flex>
                <Box>
                    <Flex justifyContent="space-between" pb={2} gap="1rem">
                        {experienceSwitch ? (
                            <>
                            <Flex direction="column" gap={2}>
                                <Text fontSize="md">Current XP:</Text>
                                <InputGroup>
                                    <NumberInput value={currentXP} onChange={(value) => setCurrentXP(value)}>
                                        <NumberInputField />
                                        <NumberInputStepper>
                                        <NumberIncrementStepper />
                                        <NumberDecrementStepper />
                                        </NumberInputStepper>
                                    </NumberInput>
                                </InputGroup>
                            </Flex>

                            <Flex direction="column" justifyContent="end">
                                <IconButton onClick={toggleColorMode}>
                                    {colorMode === 'light' ? <MoonIcon/> : <SunIcon/>}
                                </IconButton>
                            </Flex>

                            <Flex direction="column" gap={2}>
                                <Text fontSize="md">Target XP:</Text>
                                <InputGroup>
                                    <NumberInput value={targetXP} onChange={(value) => setTargetXP(value)}>
                                        <NumberInputField />
                                        <NumberInputStepper>
                                        <NumberIncrementStepper />
                                        <NumberDecrementStepper />
                                        </NumberInputStepper>
                                    </NumberInput>
                                </InputGroup>
                            </Flex>
                            </>
                        ) : (
                            <>
                            <Flex direction="column" gap={2}>
                                <Text fontSize="md">Current Level:</Text>
                                <InputGroup>
                                    <NumberInput value={currentLevel} onChange={(value) => setCurrentXP(LevelExperience[value])}>
                                        <NumberInputField />
                                        <NumberInputStepper>
                                        <NumberIncrementStepper />
                                        <NumberDecrementStepper />
                                        </NumberInputStepper>
                                    </NumberInput>
                                </InputGroup>
                            </Flex>

                            <Flex direction="column" justifyContent="end">
                                <IconButton onClick={toggleColorMode}>
                                    {colorMode === 'light' ? <MoonIcon/> : <SunIcon/>}
                                </IconButton>
                            </Flex>

                            <Flex direction="column" gap={2}>
                                <Text fontSize="md">Target Level:</Text>
                                <InputGroup>
                                    <NumberInput value={targetLevel} onChange={(value) => setTargetXP(LevelExperience[value])}>
                                        <NumberInputField />
                                        <NumberInputStepper>
                                        <NumberIncrementStepper />
                                        <NumberDecrementStepper />
                                        </NumberInputStepper>
                                    </NumberInput>
                                </InputGroup>
                            </Flex>
                            </>
                        )}
                    </Flex>
                    <Flex pb={2} justifyContent="space-between">
                        <Box padding="1rem" borderWidth='1px' borderRadius='lg'>
                            <Checkbox isChecked={selectedCheckbox === "gAltar"} size='md' colorScheme='blue' id="gAltar" onChange={() => handleCheckboxChange("gAltar", 3.5)}>
                                Gilded Altar - 350%
                            </Checkbox>
                        </Box>
                        <Box padding="1rem" borderWidth='1px' borderRadius='lg'>
                            <Checkbox isChecked={selectedCheckbox === "cAltar"} size='md' colorScheme='blue' id="cAltar" onChange={() => handleCheckboxChange("cAltar", 7)}>
                                Chaos Altar - 700%
                            </Checkbox>
                        </Box>
                        <Box padding="1rem" borderWidth='1px' borderRadius='lg'>
                            <Checkbox isChecked={selectedCheckbox === "ECto"} size='md' colorScheme='blue' id="ECto" onChange={() => handleCheckboxChange("ECto", 4)}>
                                Ectofunctus - 400%
                            </Checkbox>
                        </Box>
                    </Flex>
                    <Flex >
                        <XPOptions
                            xpDiff={parseInt(xpDiff)}
                            targetXP={parseInt(targetXP)}
                            XPMultiplier={parseInt(XPMultiplier)}
                        />
                    </Flex>
                </Box>
            </Flex>
        </Box>
    )
};

export default CalculatorUI;