
import { Flex, ChakraProvider, extendTheme } from "@chakra-ui/react";
import CalculatorUI from "./components/CalculatorUI";

const config = {
  initialColorMode: 'dark',
  useSystemColorMode: false,
}
const theme = extendTheme({ config })

function App() {
  return (
    <ChakraProvider theme={theme} initialColorMode={theme.config.initialColorMode}>
      <Flex minH="100vh" justifyContent="center" alignItems="center" overflow="auto">
        <CalculatorUI/>
      </Flex>
    </ChakraProvider>
  );
};

export default App;