import { Flex, 
    Box, 
    Table,
    Thead,
    Tbody,
    Tr,
    Th,
    Td,
    TableContainer,
    Image
} from '@chakra-ui/react';
import { ViewIcon } from '@chakra-ui/icons';
import PrayerOptions from "./data/PrayerOptions";

const XPOptions = ({xpDiff, targetXP, XPMultiplier}) => {

    return (
        <Flex>
            <Box padding={5} borderWidth='1px' borderRadius='lg' overflow="hidden">
                <TableContainer>
                    <Table variant="simple">
                        <Thead>
                            <Tr>
                                <Th> <ViewIcon boxSize={4}/> </Th>
                                <Th>Item</Th>
                                <Th>XP Given</Th>
                                <Th>Item Amount Req</Th>
                                <Th>Total XP</Th>
                            </Tr>
                        </Thead>
                        <Tbody>
                            {PrayerOptions.map(opt => (
                                <Tr key={opt.name}>
                                    <Td><Image objectFit='cover' src={opt.image} boxSize={5}/></Td>
                                    <Td>{opt.name}</Td>
                                    <Td isNumeric={true}>{opt.xp}</Td>
                                    <Td isNumeric={true}>{Math.ceil(xpDiff / (opt.xp * XPMultiplier))}</Td>
                                    <Td isNumeric={true}>{targetXP}</Td>
                                </Tr>    
                            ))}
                        </Tbody>
                    </Table>
                </TableContainer>
            </Box>
        </Flex>
    );
};

export default XPOptions;